body {
  font-size: 14px;
}
.contact-list ul li {
  padding: 0;
  list-style-type: none;
  margin-left: auto;
}
.contact-list ul {
  list-style-position: inside;
  padding-left: 0;
}

ul li a {
  text-decoration: none;
  color: white;
}

@media screen and (max-device-width: 700px) {
  body {
    font-size: 10px;
  }
  .bp3-navbar .bp3-navbar-group .bp3-button-text {
    display: none;
  }
  .kontainer {
    width: 100%;
  }

  .container .row .col {
    display: inline-block;
  }

  .skills {
    display: inline-flex;
  }
}
